.form__fields {
  display: flex;
  flex-wrap: wrap;
  margin: -10px -20px;
}

.form__fields .form__field {
  flex-grow: 1;
  padding: 10px 20px;
  margin: 0 !important;
  width: 100%;
}

.form__fields .form__field_half {
  width: 50%;
}

@media only screen and (max-width: 767px) {
  .form__fields .form__field_half {
    width: 100%;
  }
}

.form__fields .form__fields {
  margin: -10px -9px;
}

.form__fields .form__fields .form__field {
  padding: 10px 9px;
}

@media only screen and (max-width: 767px) {
  .form__fields .form__fields .form__field_half {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .form__fields .form__fields .form__field_half {
    width: 100%;
  }
}

.form__label {
  display: block;
  margin-bottom: 12px;
  color: #fff;
  font-family: Verdana;
  font-size: 18px;
  line-height: 22px;
}

.form__footer {
  margin-top: 42px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .form__btn {
    max-width: 226px;
  }
}

.cta-wrapper {
  padding-left: 25px;
  padding-right: 25px;
}

.cta-wrapper_form {
  padding-bottom: 74px;
  margin-top: -90px;
}

@media only screen and (max-width: 767px) {
  .cta-wrapper_form {
    margin-top: -42px;
    padding-bottom: 58px;
  }
}

.cta {
  padding: 47px 50px 55px;
  margin: 0 auto;
  max-width: 864px;
  border-radius: 5px;
  background: #fff;
  box-shadow: -1px 25px 20px #F2F2F2;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .cta {
    padding: 29px 20px 33px;
    box-shadow: 0px 2px 20px rgba(21, 24, 36, 0.15993);
  }
}

.cta__title {
  margin-bottom: 15px;
}

@media only screen and (max-width: 767px) {
  .cta__title {
    margin-bottom: 14px;
  }
}

.product {
  padding: 37px 20px 81px;
}

@media only screen and (max-width: 767px) {
  .product {
    padding: 30px 20px 38px;
  }
}

.product__inner {
  margin: 0 auto;
  max-width: 918px;
}

.product__logo {
  display: block;
  margin: 0 auto 41px;
  width: 264px;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .product__logo {
    margin-bottom: 26px;
    width: 208px;
  }
}

.product__logo img {
  width: 100%;
}

.product__container:after {
  content: " ";
  display: table;
  clear: both;
}

.product__figure {
  float: right;
  width: 63.83442%;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .product__figure {
    float: none;
    margin: 0 auto;
    width: 100%;
    max-width: 320px;
  }
}

.product__content {
  float: left;
  padding-top: 16px;
  width: 36.16558%;
  font-family: ArialMT, Arial, sans-serif;
}

@media only screen and (max-width: 767px) {
  .product__content {
    float: none;
    margin: 0 auto;
    width: 100%;
    max-width: 261px;
    text-align: center;
  }
}

.product__img {
  margin: 0 0 -142px -51px;
}

@media only screen and (max-width: 767px) {
  .product__img {
    margin: 0 -20px -18px;
  }
}

.product__img img {
  width: 100%;
}

.product__title {
  margin-bottom: 9px;
  color: #000;
  font-size: 46px;
  font-weight: 400;
  line-height: 64px;
}

@media only screen and (max-width: 767px) {
  .product__title {
    margin-bottom: 13px;
    font-size: 30px;
    font-weight: 700;
    line-height: 33px;
  }
}

@media only screen and (max-width: 767px) {
  .product__description {
    margin-bottom: 25px;
  }
}

.product__description p {
  margin-bottom: 32px;
  color: #263238;
  font-size: 18px;
  font-style: italic;
  line-height: 32px;
}

@media only screen and (max-width: 767px) {
  .product__description p {
    margin-bottom: 17px;
    font-family: Verdana;
    font-size: 14px;
    font-style: normal;
    line-height: 17px;
  }
}

.product__btn {
  width: 226px;
}

.content {
  padding: 48px 0 37px;
}

@media only screen and (max-width: 767px) {
  .content {
    padding: 0 0 60px;
    margin-top: -39px;
  }
}

.content__section {
  padding: 37px 0;
}

@media only screen and (max-width: 767px) {
  .content__section {
    padding: 0;
  }
}

@media only screen and (max-width: 767px) {
  .content__section:not(:last-child) {
    padding-bottom: 65px;
  }
}

.content__content {
  font-family: ArialMT, Arial, sans-serif;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .content__content {
    display: none;
  }
}

.content__title {
  margin-bottom: 9px;
}

.content__description {
  margin: 0 auto 3px;
  max-width: 619px;
  font-size: 18px;
  line-height: 32px;
}

@media only screen and (max-width: 767px) {
  .content__cta {
    padding-left: 25px;
    padding-right: 25px;
  }
}
